html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-family);
  background-color: var(--background);
  color: var(--text-color);
  min-height: 150vh;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h2 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-white: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-white: hsl(230, 22%, 74%);
  --toggle-btn-white: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --white-bg: hsl(0, 0%, 100%);
  --white-top-bg: hsl(225, 100%, 98%);
  --white-card: hsl(227, 47%, 96%);
  --white-card-hover: hsl(228, 33%, 91%);
  --white-text: hsl(228, 12%, 44%);
  --white-text2: hsl(230, 17%, 14%);
  --background: var(--white-bg);
  --text-color: var(--white-text2);
  --text-color2: var(--white-text);
  --card-bg: var(--white-card);
  --card-hover: var(--white-card-hover);
  --toggle: var(--toggle-white);
  --toggle-bg: var(--toggle-bg-white);
  --toggle-btn: var(--toggle-btn-white);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--dark-text);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
    );
    --toggle-btn: var(--dark-bg);
  }
}
body.light {
  --background: var(--white-bg);
  --text-color: var(--white-text2);
  --text-color2: var(--white-text);
  --card-bg: var(--white-card);
  --card-hover: var(--white-card-hover);
  --toggle: var(--toggle-white);
  --toggle-bg: var(--toggle-bg-white);
  --toggle-btn: var(--toggle-btn-white);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--dark-text);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-btn: var(--dark-bg);
}

:root {
  --font-family: "Inter", sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: ) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}
.header__toggle {
  margin-left: auto;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
}
.toggle label {
  font-size: 0.875rem;
  color: var(--toggle);
  font-weight: 700;
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle .radio-btn {
  margin: 0 -2px 0 0;
  opacity: 0;
  height: 1.5rem;
  width: 1.25rem;
}
.toggle .radio-btn:focus ~ .toggle__button {
  border: 1px solid white;
}
.toggle__background {
  display: block;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  height: 100%;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-btn);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background-color: var(--card-bg);
  color: var(--text-color2);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  padding-top: 1.5625rem;
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  /* border-top: rem(5) solid;
  border-image: linear-gradient(
      to right,
      var(--instagram-start),
      var(--instagram-middle),
      var(--instagram-end)
    )
    1 0 0 0;*/
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(to right, var(--instagram-start), var(--instagram-middle), var(--instagram-end));
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__platform {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
  margin-top: 1.75rem;
  flex-basis: 100%;
}
.card__count {
  color: var(--text-color);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  margin-top: 0.5rem;
  font-size: 0.75rem;
  color: var(--dark-text);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  font-weight: 400;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.25rem;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}