@use "../utils/" as *;

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: rem(24);
  margin-bottom: rem(3);
  @include breakpoint-up("large") {
    font-size: rem(28);
  }
}
a,
a:visited,
a:active {
  text-decoration: none;
}

h2 {
  font-size: rem(24);
  font-weight: 700;
  margin-bottom: rem(24);
}
